.home-page {
    flex-direction: column;
    margin-top: 0;
    gap: var(--spacing-sm);
}

.sub-section-container {
    display: flex;
    width: auto;
    gap: var(--spacing-sm);

    .sub-section-item {
        min-width: 200px;
        min-height: 300px;
        border-radius: var(--border-radius-md-1);
        flex: 0.25;
        position: relative;
        color: var(--contrast-color);
        border: 1px solid var(--sub-light-color);
        overflow: hidden;
        background-color: var(--main-color);
        box-shadow: inset 0 0 50px rgba(255, 255, 255, 0.088);
        font-size: var(--font-s-md);
        padding: var(--spacing-md);
        display: flex;
        flex-direction: column;
        justify-content: center;
        gap: var(--spacing-md);

        .section-svg {
            position: absolute;
            width: 200%;
            height: 100%;
            transition: all 0.4s ease-in;
            transform: translateX(-250px);
            z-index: 1;
        }

        .btn-sub-section {
            display: flex;
            align-items: center;
            justify-content: center;

            .btn-sub-section-icon {
                margin-right: var(--spacing-sm);
            }

            .about-us-icon {
                margin-left: var(--spacing-sm);
                margin-right: 0;
            }
        }

    }

    .sub-section-item:hover .section-svg {
        transform: translateX(-50px) translateY(120px);
    }

    .sub-section-item:nth-child(1) {
        color: var(--contrast-color);
        flex: 0.5;
        background: linear-gradient(263deg, #FDB767 0%, #414348 100%);
    }

    .sub-section-item:nth-child(2) .btn-sub-section {
        transition: opacity 0.3s ease, z-index 0.3s ease, box-shadow 0.5s ease, color 0.5s ease;
        z-index: 0;
        opacity: 0;
    }

    .sub-section-item:nth-child(2):hover .btn-sub-section {
        transition: opacity 1s ease, z-index 1s ease, box-shadow 0.5s ease, color 0.5s ease;
        z-index: 2;
        opacity: 1;
    }

    .sub-section-item:nth-child(3) {
        align-items: center;
        background-size: cover;
    }

}

